import b_ from "b_";
import cx from "classnames";
import { CSSProperties, FC } from "react";

import "./BaseButton.scss";

interface BaseButtonProps {
  onClick: () => void;
  className?: string;
  disabled?: boolean;
  clear?: boolean;
  red?: boolean;
  green?: boolean;
  autoFocus?: boolean;
  style?: CSSProperties;
}

const b = b_.with("base-button");

// todo –≤—Å–µ –ø—Ä–æ–ø—Å—ã –¥–ª—è –æ–±—ã—á–Ω–æ–π –∫–Ω–æ–ø–∫–∏, –∏ –≤–ø–∏—Å–∞—Ç—å –∏—Ö –≤ —Ç–∏–ø—ã –∫–∞–∫ –Ω–∞—Å–ª–µ–¥–æ–≤–∞–Ω–∏–µ, –∞ –Ω–µ –∫–∞–∂–¥—ã–π –æ—Ç–¥–µ–ª—å–Ω–æ
export const BaseButton: FC<BaseButtonProps> = ({
  onClick,
  className,
  children,
  disabled,
  clear,
  red,
  green,
  autoFocus,
  style,
}) => {
  return (
    <button
      className={cx(b({ clear, red, green }), className)}
      onClick={onClick}
      disabled={disabled}
      style={style}
      autoFocus={autoFocus}
    >
      {children}
    </button>
  );
};
